import uniRate from "@/components/uni-rate/uni-rate.vue" //星星评分
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue" //加载更多
export default {
	components:{
		uniRate,
		uniLoadMore
	},

	data() {
		return {
			goodsList: [],
			headerTop: 0,
			//控制滑动效果
			typeClass: 'goods',
			subState: 1,
			theIndex: null,
			oldIndex: null,
			isStop: false,
			info:{},
			current_page:1 ,
			last_page:"",
			status: "more",
		}
	},
	onPageScroll(e) {

	},
	onReachBottom() {
		// 调用获取推荐列表接口
		// 当前页小于最后一页才调用
		this.current_page++
		if (this.current_page > this.last_page) {
			return;
		} else {
			this.requestKeep(this.current_page);
		}
	
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad() {
		//兼容H5下排序栏位置










		
		var _this=this
		uni.getStorage({
			key:"info",
			success: (res) => {
			
				this.info=JSON.parse(res.data)
					console.log(res.data,this.info)
			}
		})
		
	    this.current_page=1;
		this.requestKeep(this.current_page);
	},
	methods: {
		requestKeep(page){
				//ping lun
			this.request({
				url:this.config.url+"member/comment",
				method:"post",
				data:{
					token:this.token,
					page:page,
				},
				success:(res)=> {
					console.log(res)
					if(res.data.code==1){
						this.last_page = res.data.data.last_page;
						this.goodsList=this.goodsList.concat(res.data.data.data);
						console.log(this.goodsList)
						this.status = "more";
						if (this.current_page >= this.last_page) {
							this.status = "noMore";
						}
					}
					
				}
			})
		},
		sayFeel() {
			uni.navigateTo({
				url: "/pages/user/keep/sayFeel/sayFeel"
			})
		},
		switchType(type) {
			// if(this.typeClass==type){
			// 	return ;
			// }
			// uni.pageScrollTo({
			// 	scrollTop:0,
			// 	duration:0
			// })
			// this.typeClass = type;
			// this.subState = this.typeClass==''?'':'show'+type;
			// setTimeout(()=>{
			// 	this.oldIndex = null;
			// 	this.theIndex = null;
			// 	this.subState = this.typeClass=='goods'?'':this.subState;
			// },200)
			this.typeClass = type;
			if (type == "goods") {
				this.subState = 1;

			} else if (type == "shop") {
				this.subState = 2;
			}
		},


		discard() {
			//丢弃
		}


	}
}
